import { useState } from 'react'
import { motion } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
    User,
    Phone,
    CreditCard,
    Bus,
    Calendar,
    Star,
    Plus,
    Search,
    MoreVertical,
    CheckCircle,
    Clock,
    XCircle
} from 'lucide-react'

interface Driver {
    id: string
    name: string
    phone: string
    licenseNumber: string
    assignedVehicle: string | null
    status: 'onDuty' | 'offDuty' | 'onLeave'
    rating: number
    totalTrips: number
    joinDate: string
    avatar?: string
}

// 模拟驾驶员数据
const mockDrivers: Driver[] = [
    { id: '1', name: 'M. Zhang', phone: '138****1234', licenseNumber: 'D12345678', assignedVehicle: 'SH-B12345', status: 'onDuty', rating: 4.8, totalTrips: 1250, joinDate: '2022-03-15' },
    { id: '2', name: 'Q. Li', phone: '139****2345', licenseNumber: 'D23456789', assignedVehicle: 'SH-B23456', status: 'onDuty', rating: 4.6, totalTrips: 980, joinDate: '2022-06-20' },
    { id: '3', name: 'F. Wang', phone: '137****3456', licenseNumber: 'D34567890', assignedVehicle: null, status: 'offDuty', rating: 4.9, totalTrips: 1580, joinDate: '2021-08-10' },
    { id: '4', name: 'W. Zhao', phone: '136****4567', licenseNumber: 'D45678901', assignedVehicle: 'SH-B45678', status: 'onDuty', rating: 4.5, totalTrips: 720, joinDate: '2023-01-05' },
    { id: '5', name: 'Y. Liu', phone: '135****5678', licenseNumber: 'D56789012', assignedVehicle: null, status: 'onLeave', rating: 4.7, totalTrips: 1100, joinDate: '2022-09-18' },
]

export function DriversTab() {
    const { t } = useTranslation()
    const [drivers] = useState<Driver[]>(mockDrivers)
    const [searchTerm, setSearchTerm] = useState('')
    const [statusFilter, setStatusFilter] = useState<string>('all')

    const filteredDrivers = drivers.filter(d => {
        const matchesSearch = d.name.includes(searchTerm) || d.phone.includes(searchTerm) || d.licenseNumber.includes(searchTerm)
        const matchesStatus = statusFilter === 'all' || d.status === statusFilter
        return matchesSearch && matchesStatus
    })

    const getStatusBadge = (status: Driver['status']) => {
        const styles = {
            onDuty: 'bg-green-500/10 text-green-600 dark:text-green-400',
            offDuty: 'bg-slate-500/10 text-slate-600 dark:text-slate-400',
            onLeave: 'bg-amber-500/10 text-amber-600 dark:text-amber-400',
        }
        return styles[status]
    }

    const getStatusIcon = (status: Driver['status']) => {
        switch (status) {
            case 'onDuty': return CheckCircle
            case 'offDuty': return Clock
            case 'onLeave': return XCircle
        }
    }

    const stats = [
        { label: t('transit.driverStatus.onDuty'), value: drivers.filter(d => d.status === 'onDuty').length, color: 'green', icon: CheckCircle },
        { label: t('transit.driverStatus.offDuty'), value: drivers.filter(d => d.status === 'offDuty').length, color: 'slate', icon: Clock },
        { label: t('transit.driverStatus.onLeave'), value: drivers.filter(d => d.status === 'onLeave').length, color: 'amber', icon: XCircle },
    ]

    return (
        <div className="space-y-6">
            {/* 统计卡片 */}
            <div className="grid grid-cols-3 gap-4">
                {stats.map((stat, i) => (
                    <motion.div
                        key={i}
                        initial={{ opacity: 0, y: 10 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ delay: i * 0.1 }}
                        className="p-4 rounded-xl bg-white dark:bg-dark-800 border border-slate-200 dark:border-dark-600"
                    >
                        <div className="flex items-center gap-3">
                            <div className={`p-2 rounded-lg bg-${stat.color}-500/10`}>
                                <stat.icon className={`w-5 h-5 text-${stat.color}-500`} />
                            </div>
                            <div>
                                <p className="text-sm text-slate-500">{stat.label}</p>
                                <p className="text-2xl font-bold text-slate-900 dark:text-slate-100">{stat.value}</p>
                            </div>
                        </div>
                    </motion.div>
                ))}
            </div>

            {/* 搜索和筛选 */}
            <div className="flex items-center justify-between gap-4">
                <div className="flex items-center gap-4 flex-1">
                    <div className="relative flex-1 max-w-md">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-slate-400" />
                        <input
                            type="text"
                            placeholder={t('common.searchPlaceholder')}
                            value={searchTerm}
                            onChange={(e) => setSearchTerm(e.target.value)}
                            className="w-full pl-10 pr-4 py-2 bg-slate-50 dark:bg-dark-700 border border-slate-200 dark:border-dark-600 rounded-lg text-sm"
                        />
                    </div>
                    <select
                        value={statusFilter}
                        onChange={(e) => setStatusFilter(e.target.value)}
                        className="px-4 py-2 bg-slate-50 dark:bg-dark-700 border border-slate-200 dark:border-dark-600 rounded-lg text-sm"
                    >
                        <option value="all">{t('common.allStatus')}</option>
                        <option value="onDuty">{t('transit.driverStatus.onDuty')}</option>
                        <option value="offDuty">{t('transit.driverStatus.offDuty')}</option>
                        <option value="onLeave">{t('transit.driverStatus.onLeave')}</option>
                    </select>
                </div>
                <button className="btn-primary flex items-center gap-2">
                    <Plus className="w-4 h-4" />
                    {t('common.add')}
                </button>
            </div>

            {/* 驾驶员卡片列表 */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                {filteredDrivers.map((driver, i) => {
                    const StatusIcon = getStatusIcon(driver.status)
                    return (
                        <motion.div
                            key={driver.id}
                            initial={{ opacity: 0, scale: 0.95 }}
                            animate={{ opacity: 1, scale: 1 }}
                            transition={{ delay: i * 0.05 }}
                            className="p-4 bg-white dark:bg-dark-800 rounded-xl border border-slate-200 dark:border-dark-600 hover:border-primary-500/30 transition-colors"
                        >
                            <div className="flex items-start justify-between mb-4">
                                <div className="flex items-center gap-3">
                                    <div className="w-12 h-12 rounded-full bg-gradient-to-br from-primary-500 to-accent-500 flex items-center justify-center text-white font-bold text-lg">
                                        {driver.name.charAt(0)}
                                    </div>
                                    <div>
                                        <h3 className="font-semibold text-slate-900 dark:text-slate-100">{driver.name}</h3>
                                        <div className="flex items-center gap-1 text-amber-500">
                                            <Star className="w-3 h-3 fill-current" />
                                            <span className="text-xs">{driver.rating}</span>
                                        </div>
                                    </div>
                                </div>
                                <span className={`inline-flex items-center gap-1 px-2 py-0.5 rounded-full text-xs font-medium ${getStatusBadge(driver.status)}`}>
                                    <StatusIcon className="w-3 h-3" />
                                    {t(`transit.driverStatus.${driver.status}`)}
                                </span>
                            </div>

                            <div className="space-y-2 text-sm">
                                <div className="flex items-center gap-2 text-slate-600 dark:text-slate-400">
                                    <Phone className="w-4 h-4" />
                                    <span>{driver.phone}</span>
                                </div>
                                <div className="flex items-center gap-2 text-slate-600 dark:text-slate-400">
                                    <CreditCard className="w-4 h-4" />
                                    <span>{driver.licenseNumber}</span>
                                </div>
                                <div className="flex items-center gap-2 text-slate-600 dark:text-slate-400">
                                    <Bus className="w-4 h-4" />
                                    <span>{driver.assignedVehicle || t('transit.notAssigned')}</span>
                                </div>
                            </div>

                            <div className="flex items-center justify-between mt-4 pt-4 border-t border-slate-200 dark:border-dark-600">
                                <div className="text-xs text-slate-500">
                                    {t('transit.totalTrips')}: <span className="font-medium text-slate-700 dark:text-slate-300">{driver.totalTrips}</span>
                                </div>
                                <button className="p-1.5 hover:bg-slate-100 dark:hover:bg-dark-700 rounded-lg transition-colors">
                                    <MoreVertical className="w-4 h-4 text-slate-400" />
                                </button>
                            </div>
                        </motion.div>
                    )
                })}
            </div>
        </div>
    )
}

export default DriversTab
